"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { CreditCard, Building2, Smartphone, ShieldCheck, Lock, ArrowLeft, ArrowRight, User, CheckCircle2 } from "lucide-react"
import { CartItem, useCart } from "@/contexts/cart-context"

interface CheckoutModalProps {
  isOpen: boolean
  onClose: () => void
  product?: {
    name: string
    price: number
    originalPrice: number
    savings: number
  }
  cartItems?: CartItem[]
  asPage?: boolean
}

type Step = 1 | 2

export function CheckoutModal({ 
  isOpen, 
  onClose, 
  product: initialProduct, 
  cartItems,
  asPage = false 
}: CheckoutModalProps) {
  const [currentStep, setCurrentStep] = useState<Step>(1)
  const [isProcessing, setIsProcessing] = useState(false)
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [touched, setTouched] = useState<Record<string, boolean>>({})
  const { clearCart } = useCart()

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
  })

  // Calcular totales basado en cartItems o product
  const getItems = (): CartItem[] => {
    if (cartItems && cartItems.length > 0) {
      return cartItems
    }
    if (initialProduct) {
      return [{
        id: 0,
        name: initialProduct.name,
        price: initialProduct.price,
        originalPrice: initialProduct.originalPrice,
        quantity: 1,
        savings: initialProduct.savings,
      }]
    }
    return []
  }

  const items = getItems()
  const total = items.reduce((sum, item) => sum + item.price * item.quantity, 0)
  const totalSavings = items.reduce((sum, item) => sum + (item.originalPrice - item.price) * item.quantity, 0)
  const subtotal = items.reduce((sum, item) => sum + item.originalPrice * item.quantity, 0)

  const validateEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    return emailRegex.test(email)
  }

  const validatePhone = (phone: string): boolean => {
    const phoneRegex = /^[0-9]{10}$/
    return phoneRegex.test(phone.replace(/\s/g, ""))
  }

  const validateField = (name: string, value: string): string => {
    switch (name) {
      case "name":
        if (!value.trim()) return "El nombre es requerido"
        if (value.trim().length < 2) return "El nombre debe tener al menos 2 caracteres"
        return ""
      case "email":
        if (!value.trim()) return "El email es requerido"
        if (!validateEmail(value)) return "Ingresa un email v√°lido"
        return ""
      case "phone":
        if (!value.trim()) return "El tel√©fono es requerido"
        if (!validatePhone(value)) return "Ingresa un tel√©fono v√°lido (10 d√≠gitos)"
        return ""
      default:
        return ""
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))

    if (touched[name]) {
      const error = validateField(name, value)
      setErrors((prev) => ({ ...prev, [name]: error }))
    }
  }

  const handleBlur = (name: string) => {
    setTouched((prev) => ({ ...prev, [name]: true }))
    const value = formData[name as keyof typeof formData]
    const error = validateField(name, value as string)
    setErrors((prev) => ({ ...prev, [name]: error }))
  }

  const validateStep = (): boolean => {
    const stepErrors: Record<string, string> = {}
    const fields = ["name", "email", "phone"]
    
    fields.forEach((field) => {
      const value = formData[field as keyof typeof formData] as string
      const error = validateField(field, value)
      if (error) stepErrors[field] = error
    })

    setErrors(stepErrors)
    fields.forEach((field) => {
      setTouched((prev) => ({ ...prev, [field]: true }))
    })

    return Object.keys(stepErrors).length === 0
  }

  const handleNext = () => {
    if (validateStep()) {
      setCurrentStep(2)
    }
  }

  const handleBack = () => {
    if (currentStep > 1) {
      setCurrentStep(1)
    }
  }

  const createWompiSession = async () => {
    const description = items.map(item => 
      `${item.quantity}x ${item.name}`
    ).join(", ")

    const wompiData = {
      name: formData.name,
      email: formData.email,
      phone: formData.phone,
      amount: total,
      currency: "COP",
      reference: `AMAZONIICO-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      description: `Amazoniico Col√°geno Marino - ${description}`,
      cartItems: items,
    }

    try {
      const response = await fetch("/api/wompi/create-session", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(wompiData),
      })

      const data = await response.json()

      if (!response.ok) {
        const errorMessage = data.error || "Error al crear sesi√≥n de pago"
        alert(`${errorMessage}\n\nPor favor verifica tus datos e intenta de nuevo.`)
        setIsProcessing(false)
        return
      }

      if (data.checkoutUrl) {
        clearCart()
        window.location.href = data.checkoutUrl
      } else {
        throw new Error(`No se recibi√≥ la URL de checkout`)
      }
    } catch (error) {
      console.error("Error:", error)
      const errorMessage = error instanceof Error ? error.message : "Error desconocido"
      alert(`Hubo un error al procesar el pago: ${errorMessage}\n\nPor favor intenta de nuevo o contacta a soporte.`)
      setIsProcessing(false)
    }
  }

  const handleSubmit = async () => {
    setIsProcessing(true)
    await createWompiSession()
  }

  // Resetear cuando se cierra
  useEffect(() => {
    if (!isOpen) {
      setCurrentStep(1)
      setFormData({ name: "", email: "", phone: "" })
      setErrors({})
      setTouched({})
      setIsProcessing(false)
    }
  }, [isOpen])

  const renderStepContent = () => {
    switch (currentStep) {
      case 1:
        return (
          <div className="space-y-4">
            <div className="-mt-2">
              <h3 className="font-bold text-lg text-foreground mb-2">Datos B√°sicos</h3>
              <p className="text-sm text-muted-foreground">
                Solo necesitamos esta informaci√≥n para procesar tu pedido.
              </p>
            </div>
            <div className="space-y-1">
              <Input
                id="name"
                name="name"
                required
                placeholder="Nombre completo *"
                className={`rounded-xl ${errors.name && touched.name ? "border-destructive focus-visible:ring-destructive" : ""}`}
                value={formData.name}
                onChange={handleInputChange}
                onBlur={() => handleBlur("name")}
              />
              {errors.name && touched.name && (
                <p className="text-xs text-destructive mt-1">{errors.name}</p>
              )}
            </div>
            <div className="space-y-1">
              <Input
                id="email"
                name="email"
                type="email"
                required
                placeholder="Email *"
                className={`rounded-xl ${errors.email && touched.email ? "border-destructive focus-visible:ring-destructive" : ""}`}
                value={formData.email}
                onChange={handleInputChange}
                onBlur={() => handleBlur("email")}
              />
              {errors.email && touched.email && (
                <p className="text-xs text-destructive mt-1">{errors.email}</p>
              )}
            </div>
            <div className="space-y-1">
              <Input
                id="phone"
                name="phone"
                type="tel"
                required
                placeholder="WhatsApp/Tel√©fono *"
                className={`rounded-xl ${errors.phone && touched.phone ? "border-destructive focus-visible:ring-destructive" : ""}`}
                value={formData.phone}
                onChange={handleInputChange}
                onBlur={() => handleBlur("phone")}
              />
              {errors.phone && touched.phone && (
                <p className="text-xs text-destructive mt-1">{errors.phone}</p>
              )}
            </div>
          </div>
        )

      case 2:
        return (
          <div className="space-y-6">
            <div className="text-center space-y-2">
              <h3 className="font-bold text-xl text-foreground">
                Ser√°s redirigido a Wompi para completar tu pago
              </h3>
              <p className="text-sm text-muted-foreground">
                En Wompi podr√°s completar tu informaci√≥n de env√≠o y seleccionar tu m√©todo de pago
              </p>
            </div>

            {/* Resumen del pedido */}
            <div className="bg-muted/30 rounded-xl p-4 space-y-3">
              <h4 className="font-bold text-base text-foreground">Resumen del Pedido</h4>
              <div className="space-y-2">
                {items.map((item) => (
                  <div key={item.id} className="flex justify-between text-sm">
                    <span className="text-muted-foreground">
                      {item.quantity}x {item.name}
                    </span>
                    <span className="font-medium text-foreground">
                      ${(item.price * item.quantity).toLocaleString("es-CO")}
                    </span>
                  </div>
                ))}
                {subtotal > total && (
                  <div className="flex justify-between text-sm pt-2 border-t">
                    <span className="text-muted-foreground">Subtotal:</span>
                    <span className="text-muted-foreground">${subtotal.toLocaleString("es-CO")}</span>
                  </div>
                )}
                {totalSavings > 0 && (
                  <div className="flex justify-between text-sm text-primary font-medium">
                    <span>Ahorro:</span>
                    <span>-${totalSavings.toLocaleString("es-CO")}</span>
                  </div>
                )}
                <div className="flex justify-between font-bold pt-2 border-t">
                  <span>Total:</span>
                  <span className="text-primary text-lg">
                    ${total.toLocaleString("es-CO")}
                  </span>
                </div>
              </div>
            </div>

            {/* Informaci√≥n de Wompi */}
            <div className="bg-muted/30 rounded-xl p-4 space-y-3">
              <h4 className="font-bold text-base text-foreground">En Wompi completar√°s:</h4>
              <ul className="space-y-2 text-sm text-muted-foreground">
                <li className="flex items-start gap-2">
                  <CheckCircle2 className="h-4 w-4 text-primary mt-0.5 flex-shrink-0" />
                  <span>Direcci√≥n de env√≠o completa</span>
                </li>
                <li className="flex items-start gap-2">
                  <CheckCircle2 className="h-4 w-4 text-primary mt-0.5 flex-shrink-0" />
                  <span>Tipo y n√∫mero de documento</span>
                </li>
                <li className="flex items-start gap-2">
                  <CheckCircle2 className="h-4 w-4 text-primary mt-0.5 flex-shrink-0" />
                  <span>Selecci√≥n de m√©todo de pago</span>
                </li>
              </ul>
            </div>

            {/* Medios de pago disponibles */}
            <div className="space-y-3">
              <h4 className="font-bold text-base text-foreground">Medios de Pago Disponibles:</h4>
              <div className="grid gap-3">
                <div className="flex items-center gap-3 border-2 rounded-xl p-3 border-primary/30 bg-primary/5">
                  <div className="bg-primary/10 p-2 rounded-lg">
                    <Building2 className="h-5 w-5 text-primary" />
                  </div>
                  <div className="flex-1">
                    <div className="font-semibold text-sm text-foreground">PSE</div>
                    <div className="text-xs text-muted-foreground">D√©bito directo desde tu banco</div>
                  </div>
                </div>
                <div className="flex items-center gap-3 border-2 rounded-xl p-3 border-primary/30 bg-primary/5">
                  <div className="bg-primary/10 p-2 rounded-lg">
                    <CreditCard className="h-5 w-5 text-primary" />
                  </div>
                  <div className="flex-1">
                    <div className="font-semibold text-sm text-foreground">Tarjetas de Cr√©dito/D√©bito</div>
                    <div className="text-xs text-muted-foreground">Visa, Mastercard, AmEx</div>
                  </div>
                </div>
                <div className="flex items-center gap-3 border-2 rounded-xl p-3 border-primary/30 bg-primary/5">
                  <div className="bg-primary/10 p-2 rounded-lg">
                    <Smartphone className="h-5 w-5 text-primary" />
                  </div>
                  <div className="flex-1">
                    <div className="font-semibold text-sm text-foreground">Bot√≥n Bancolombia</div>
                    <div className="text-xs text-muted-foreground">Pago r√°pido desde tu app</div>
                  </div>
                </div>
              </div>
            </div>

            {/* Informaci√≥n de seguridad */}
            <div className="flex items-center justify-center gap-1.5 px-3 py-2 bg-gradient-to-r from-primary/5 via-accent/5 to-primary/5 border border-primary/20 rounded-full">
              <div className="bg-primary/10 p-1 rounded-full">
                <ShieldCheck className="h-3 w-3 text-primary" />
              </div>
              <span className="text-[10px] font-medium text-foreground">
                Pago 100% seguro ‚Ä¢ Encriptaci√≥n SSL ‚Ä¢ Certificado por Wompi
              </span>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  const checkoutContent = (
    <div className={`${asPage ? "min-h-screen w-full" : "w-full h-full"} flex flex-col`}>
      <div className={`flex-shrink-0 px-4 md:px-6 pt-4 md:pt-6 pb-0 ${asPage ? "bg-background" : ""}`}>
        {asPage ? (
          <div className="mb-4">
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-2">
                <Lock className="h-5 w-5 text-primary" />
                <h1 className="text-xl md:text-2xl font-bold">Checkout Seguro</h1>
              </div>
            </div>
          </div>
        ) : (
          <DialogHeader>
            <DialogTitle className="text-xl md:text-2xl flex items-center gap-2">
              <Lock className="h-5 w-5 text-primary" />
              Checkout Seguro
            </DialogTitle>
          </DialogHeader>
        )}

        {/* Progress Steps */}
        <div className={`flex items-center mb-4 md:mb-6 gap-2 ${asPage ? "mt-2" : "mt-6"}`}>
          <div className="flex items-center flex-1">
            <div className="flex flex-col items-center w-full">
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center border-2 transition-all flex-shrink-0 ${
                  currentStep >= 1
                    ? "bg-primary border-primary text-primary-foreground"
                    : "bg-background border-border text-muted-foreground"
                }`}
              >
                <User className="h-5 w-5" />
              </div>
            </div>
            <div className={`h-0.5 flex-1 mx-2 transition-all ${currentStep >= 2 ? "bg-primary" : "bg-border"}`} />
            <div className="flex flex-col items-center w-full">
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center border-2 transition-all flex-shrink-0 ${
                  currentStep >= 2
                    ? "bg-primary border-primary text-primary-foreground"
                    : "bg-background border-border text-muted-foreground"
                }`}
              >
                <ShieldCheck className="h-5 w-5" />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Step Content */}
      <div className={`flex-1 overflow-y-auto px-4 md:px-6 min-h-0 ${asPage ? "pb-20" : ""}`}>
        <div className="pb-4 pt-2">
          {renderStepContent()}
        </div>
      </div>

      {/* Navigation Buttons */}
      <div className={`flex-shrink-0 px-4 md:px-6 pt-4 pb-6 bg-background border-t ${asPage ? "fixed bottom-0 left-0 right-0 z-50" : ""}`}>
        <div className="flex gap-3">
          {currentStep > 1 && (
            <Button
              type="button"
              variant="outline"
              onClick={handleBack}
              className="flex-1 rounded-full"
            >
              <ArrowLeft className="mr-2 h-4 w-4" />
              Atr√°s
            </Button>
          )}
          {currentStep < 2 ? (
            <Button
              type="button"
              onClick={handleNext}
              className="flex-1 rounded-full bg-primary"
            >
              Continuar
              <ArrowRight className="ml-2 h-4 w-4" />
            </Button>
          ) : (
            <Button
              type="button"
              onClick={handleSubmit}
              className="flex-1 rounded-full bg-primary"
              disabled={isProcessing}
            >
              {isProcessing ? (
                <>
                  <span className="animate-spin mr-2">üîÑ</span>
                  Procesando...
                </>
              ) : (
                <>
                  Continuar a Wompi
                  <ArrowRight className="ml-2 h-4 w-4" />
                </>
              )}
            </Button>
          )}
        </div>
      </div>
    </div>
  )

  if (asPage) {
    return <div className="min-h-screen bg-background">{checkoutContent}</div>
  }

  if (!isOpen) {
    return null
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl h-[90vh] flex flex-col p-0 overflow-hidden gap-0">
        {checkoutContent}
      </DialogContent>
    </Dialog>
  )
}
